import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, formValueSelector } from "redux-form";
import AccordCashMulti from "../Element/AccordCashMulti";
import AccordTransferMulti from "../Element/AccordTransferMulti";
import AccordCiok from "../Element/AccordCiok";
import AccordRongsok from "../Element/AccordRongsok";
import AccordGiro from "../Element/AccordGiro";
import Skeleton from "react-loading-skeleton";
import Swal from "sweetalert2";
import {
  setCashTemp,
  setGiroTemp,
  setTransferTemp,
} from "../../actions/customerAction";
import {
  setTampungCiok,
  settampungRongsok,
} from "../../actions/pembayaranSupplierAction";
import Axios from "axios";
import SelectSearch from "react-select-search";
import EditComponent from "../../Stocking/Component/EditComponent";

const FormCiok = lazy(() => import("./FormCiok"));
const FormRongsok = lazy(() => import("./FormRongsok"));
const FormCash = lazy(() => import("./FormCash"));

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const server = process.env.REACT_APP_BACKEND_URL;
class FormTitipanSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listCustomer: [],
      listLokasi: [],
      listSales: [],
    };
  }

  onChange() {
    this.props.change("cashNetto", this.props.cashNetto);
    this.props.change("transferNetto", this.props.transferNetto);
  }

  handleSubmitCiok(data) {
    var kode = data.kodeBarang;
    var split = kode.split("|");
    var isi = {
      kodeBarang: split[0],
      netto: data.netto,
      bruto: data.bruto,
      kadarTransaksi: data.kadarTransaksi,
    };
    var ciokTemp = JSON.parse(localStorage.getItem("ciokTemp")) || [];
    ciokTemp.push(isi);
    localStorage.setItem("ciokTemp", JSON.stringify(ciokTemp));
    var alatBayarBarang =
      JSON.parse(localStorage.getItem("alatBayarBarang")) || [];
    alatBayarBarang.push(isi);
    localStorage.setItem("alatBayarBarang", JSON.stringify(alatBayarBarang));
    this.props.dispatch(setTampungCiok());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.setTotal();
  }
  handleSubmitRongsok(data) {
    var kode = data.kodeBarang;
    var split = kode.split("|");
    var isi = {
      kodeBarang: split[0],
      netto: data.netto,
      bruto: data.bruto,
      kadarTransaksi: data.kadarTransaksi,
    };
    var rongsokTemp = JSON.parse(localStorage.getItem("rongsokTemp")) || [];
    rongsokTemp.push(isi);
    localStorage.setItem("rongsokTemp", JSON.stringify(rongsokTemp));
    var alatBayarBarang =
      JSON.parse(localStorage.getItem("alatBayarBarang")) || [];
    alatBayarBarang.push(isi);
    localStorage.setItem("alatBayarBarang", JSON.stringify(alatBayarBarang));
    this.props.dispatch(settampungRongsok());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.setTotal();
  }
  handleSubmitCash(data) {
    var isi = {
      tunaiRp: data.bruto,
      tunaiKurs: data.kadarTransaksi,
      tunaiTipe: "BON GR",
      tunaiNetto: data.netto,
    };
    var cashTemp = JSON.parse(localStorage.getItem("cashTemp")) || [];
    cashTemp.push(isi);
    localStorage.setItem("cashTemp", JSON.stringify(cashTemp));
    var alatBayarTunai =
      JSON.parse(localStorage.getItem("alatBayarTunai")) || [];
    alatBayarTunai.push(isi);
    localStorage.setItem("alatBayarTunai", JSON.stringify(alatBayarTunai));
    this.props.dispatch(setCashTemp());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayar", this.props.totalBayar);
  }
  setCiok() {
    this.props.change("ciokBrutto", this.props.totalCiok);
    this.props.change("ciokNetto", this.props.totalCiok);
    this.props.change("totalBayar", this.props.totalBayar);
  }
  setTotal() {
    this.props.change("totalBayar", this.props.totalBayar);
    this.props.change(
      "discountGr",
      this.props.discountGr === "NaN" ? 0 : this.props.discountGr
    );
  }
  setRongsok() {
    this.props.change("rongsokBruto", this.props.totalRongsok);
    this.props.change("rongsokNetto", this.props.totalRongsok);
    this.props.change("totalBayar", this.props.totalBayar);
  }
  handleSubmitTransfer(data) {
    var isi = {
      transferKurs: data.kadarTransaksi,
      transferNetto: data.netto,
      transferRp: data.bruto,
    };
    var transferTemp = JSON.parse(localStorage.getItem("transferTemp")) || [];
    transferTemp.push(isi);
    localStorage.setItem("transferTemp", JSON.stringify(transferTemp));
    var alatBayarTrf = JSON.parse(localStorage.getItem("alatBayarTrf")) || [];
    alatBayarTrf.push(isi);
    localStorage.setItem("alatBayarTrf", JSON.stringify(alatBayarTrf));
    this.props.dispatch(setTransferTemp());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayar", this.props.totalBayar);
  }
  handleSubmitGiro(data) {
    var isi = {
      giroKurs: data.kadarTransaksi,
      giroNetto: data.netto,
      giroRp: data.bruto,
    };
    var giroTemp = JSON.parse(localStorage.getItem("giroTemp")) || [];
    giroTemp.push(isi);
    localStorage.setItem("giroTemp", JSON.stringify(giroTemp));
    var alatBayarGiro = JSON.parse(localStorage.getItem("alatBayarGiro")) || [];
    alatBayarGiro.push(isi);
    localStorage.setItem("alatBayarGiro", JSON.stringify(alatBayarGiro));
    this.props.dispatch(setGiroTemp());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayar", this.props.totalBayar);
  }
  handleSubmitTransferHeader(data) {
    var isi = {
      transferTipe: "BON GR",
      accFrom: data.supplier,
      accTo: data.tujuan,
      transferTanggalJanji: data.tanggal,
    };
    var transferTemp = JSON.parse(localStorage.getItem("transferTemp")) || [];
    transferTemp.push(isi);
    localStorage.setItem("transferTemp", JSON.stringify(transferTemp));
    var alatBayarTrf = JSON.parse(localStorage.getItem("alatBayarTrf")) || [];
    alatBayarTrf.push(isi);
    localStorage.setItem("alatBayarTrf", JSON.stringify(alatBayarTrf));
    this.props.dispatch(setTransferTemp());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayar", this.props.totalBayar);
  }
  handleSubmitGiroHeader(data) {
    var isi = {
      giroTipe: "BON GR",
      giroNomorGiro: data.noGiro,
      giroBankGiro: data.bankGiro,
      accTo: data.tujuan,
      giroTanggalJatuhTempo: data.tanggal,
    };
    var giroTemp = JSON.parse(localStorage.getItem("giroTemp")) || [];
    giroTemp.push(isi);
    localStorage.setItem("giroTemp", JSON.stringify(giroTemp));
    //
    var alatBayarGiro = JSON.parse(localStorage.getItem("alatBayarGiro")) || [];
    alatBayarGiro.push(isi);
    localStorage.setItem("alatBayarGiro", JSON.stringify(alatBayarGiro));
    //
    this.props.dispatch(setGiroTemp());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayar", this.props.totalBayar);
  }

  componentDidMount() {
    Axios.get(server + "storages2?tipeData=LOKASI").then((res) =>
      this.setState({
        listLokasi: res.data,
      })
    );
    Axios.get(server + "storages2?tipeData=SALES").then((res) =>
      this.setState({
        listSales: res.data,
      })
    );
    Axios.get(server + "customers").then((res) =>
      this.setState({
        listCustomer: res.data,
      })
    );
  }
  onSubmitToko(data, input) {
    var split = data.split("|");
    this.setState({
      [input]: split[0],
      customer: split[1],
    });
    console.log(data);
    this.props.change("namaCustomer", split[1]);
    this.props.change("namaToko", split[2]);
  }
  onSubmitSales(data) {
    this.props.change("sales", data);
  }

  render() {
    var namaToko = this.state.listCustomer.map((obj) => {
      var payload = {
        value: obj.kodeCustomer + "|" + obj.namaCustomer + "|" + obj.namaToko,
        name: obj.namaToko,
      };
      return payload;
    });
    var sales = this.state.listSales.map((obj) => {
      var payload = {
        value: obj.kodeLokasi,
        name: obj.namaLokasi,
      };
      return payload;
    });
    return (
      <>
        <div className="row">
          <div className="col-lg-9">
            <h1>Titipan GR Customer</h1>
          </div>
          {/* <div className="col-lg-3">
            <div className="text-right">
              <button
                className="btn btn-warning"
                data-target="#editModal"
                data-toggle="modal"
              >
                Edit Titipan GR
              </button>
            </div>
          </div> */}
          <div className="col-lg-12">
            <br />
          </div>
          <AccordCashMulti
            sumNetto={this.props.sumTunaiNetto}
            sumTunai={this.props.sumTunai}
          />
          <AccordTransferMulti
            onSubmit={(data) => this.handleSubmitTransferHeader(data)}
            sumTransfer={this.props.sumTransfer}
            sumTransferNetto={this.props.sumTransferNetto}
          />
          <AccordGiro
            onSubmit={(data) => this.handleSubmitGiroHeader(data)}
            sumGiro={this.props.sumGiro}
            sumGiroNetto={this.props.sumGiroNetto}
          />
          <div className="col-lg-6"></div>
          <AccordCiok
            totalCiok={this.props.totalCiok}
            totalBrutoCiok={this.props.totalBrutoCiok}
          />
          <AccordRongsok
            totalRongsok={this.props.totalRongsok}
            totalBrutoRongsok={this.props.totalBrutoRongsok}
          />
        </div>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-3 col-md-3">
              <Field
                name="noBon"
                component={renderField}
                type="text"
                label="No Bon"
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="tanggal"
                label="Tanggal"
                type="date"
                component={renderField}
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <div className="form-group">
                <label htmlFor="">Tipe Titipan</label>
                <div className="form-control-transparant">
                  <label className="mr-3">
                    <Field
                      name="tipeTitipan"
                      component="input"
                      type="radio"
                      value="Tambah"
                      className="mr-2"
                    />
                    Tambah
                  </label>
                  <label>
                    <Field
                      name="tipeTitipan"
                      component="input"
                      type="radio"
                      value="Tarik"
                      className="mr-2"
                    />
                    Tarik
                  </label>
                </div>
              </div>
            </div>
            <div className="col-md-3 col-lg-3">
              <div className="form-group">
                <label htmlFor="">Tipe Titipan</label>
                <div className="form-control-transparant">
                  <label className="mr-3">
                    <Field
                      name="tipeTransaksi"
                      component="input"
                      type="radio"
                      value="Rupiah"
                      className="mr-2"
                    />
                    Rupiah
                  </label>
                  <label>
                    <Field
                      name="tipeTransaksi"
                      component="input"
                      type="radio"
                      value="Barang"
                      className="mr-2"
                    />
                    Barang
                  </label>
                </div>
              </div>
            </div>
            <div className="col-lg-6">
              <Field
                name="noBonFaktur"
                label="Nomor Bon Faktur"
                type="text"
                component={renderField}
              />
            </div>
            <div className="col-lg-6 mb-3">
              <label htmlFor="">Sales</label>
              <SelectSearch
                options={sales}
                search
                placeholder="Silahkan Pilih"
                onChange={(data) => this.onSubmitSales(data)}
              />
            </div>
            <div className="col-md-4 col-lg-4 d-none">
              <Field
                name="sales"
                label="Sales"
                type="date"
                component={renderField}
              />
            </div>
            <div className="col-lg-6 mb-3">
              <label htmlFor="">Nama Toko</label>
              <SelectSearch
                options={namaToko}
                search
                placeholder="Silahkan Pilih"
                onChange={(data) => this.onSubmitToko(data, "namaToko")}
              />
            </div>
            <div className="col-md-6 col-lg-6 d-none">
              <Field
                name="namaToko"
                component={renderField}
                type="text"
                label="Nama Toko"
              />
            </div>
            <div className="col-md-6 col-lg-6">
              <Field
                name="namaCustomer"
                component={renderField}
                type="text"
                label="Nama Customer"
              />
            </div>
            <div className="col-md-6 col-lg-6">
              <Field
                name="keterangan"
                component={renderField}
                type="text"
                label="Keterangan"
              />
            </div>
            <div className="col-lg-12">
              <div className="card">
                <div className="row p-3">
                  <div className="col-lg-4 col-md-4">
                    <div className="form-group">
                      <Field
                        name="totalBayar"
                        label="Total Bayar"
                        type="number"
                        component={renderField}
                        onBlur={this.setTotal()}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  className="btn btn-primary"
                  type="submit"
                  disabled={this.props.onSend}
                >
                  Simpan
                  <i className="fa fa-paper-plane ml-3"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
        <div
          id="ciokModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Ciok
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Suspense
                  fallback={
                    <div>
                      <Skeleton width={760} height={421} />
                    </div>
                  }
                >
                  <FormCiok
                    data={this.props.tampungCiok}
                    from="CK"
                    onSubmit={(data) => this.handleSubmitCiok(data)}
                  />
                </Suspense>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="rongsokModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Ciok
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Suspense
                  fallback={
                    <div>
                      <Skeleton width={760} height={421} />
                    </div>
                  }
                >
                  <FormRongsok
                    onSubmit={(data) => this.handleSubmitRongsok(data)}
                    from="RSK"
                    data={this.props.tampungRongsok}
                  />
                </Suspense>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="cashModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Cash
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Suspense
                  fallback={
                    <div>
                      <Skeleton width={760} height={421} />
                    </div>
                  }
                >
                  <FormCash
                    onSubmit={(data) => this.handleSubmitCash(data)}
                    data={this.props.tampungCash}
                    from="cash"
                  />
                </Suspense>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="transferModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Transfer
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Suspense
                  fallback={
                    <div>
                      <Skeleton width={760} height={421} />
                    </div>
                  }
                >
                  <FormCash
                    onSubmit={(data) => this.handleSubmitTransfer(data)}
                    data={this.props.tampungTransfer}
                    from="transfer"
                  />
                </Suspense>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="giroModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Giro
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Suspense
                  fallback={
                    <div>
                      <Skeleton width={760} height={421} />
                    </div>
                  }
                >
                  <FormCash
                    onSubmit={(data) => this.handleSubmitGiro(data)}
                    data={this.props.tampungGiro}
                    from="giro"
                  />
                </Suspense>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Giro
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Suspense
                  fallback={
                    <div>
                      <Skeleton width={760} height={421} />
                    </div>
                  }
                >
                  <EditComponent
                    onSubmit={(data) => this.handleSubmitGiro(data)}
                  />
                </Suspense>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

FormTitipanSupplier = reduxForm({
  form: "FormTitipanSupplier",
  enableReinitialize: true,
})(FormTitipanSupplier);
const selector = formValueSelector("FormTitipanSupplier"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, transfer, discount, persenan, StatusTitipan } = selector(
    state,
    "harga",
    "cash",
    "transfer",
    "discount",
    "persenan",
    "StatusTitipan"
  );

  var bayarTrf = JSON.parse(localStorage.getItem("totalTransfer"))
    ? JSON.parse(localStorage.getItem("totalTransfer"))
    : 0;
  var bayarTunai = JSON.parse(localStorage.getItem("TotalTunai"))
    ? JSON.parse(localStorage.getItem("TotalTunai"))
    : 0;
  var totalCiok = JSON.parse(localStorage.getItem("totalCiok"))
    ? JSON.parse(localStorage.getItem("totalCiok"))
    : 0;
  var totalRosok = JSON.parse(localStorage.getItem("totalRongsok"))
    ? JSON.parse(localStorage.getItem("totalRongsok"))
    : 0;
  var totalGiro = JSON.parse(localStorage.getItem("totalGiro"))
    ? JSON.parse(localStorage.getItem("totalGiro"))
    : 0;
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
    discountGr: (parseFloat(discount) * (parseFloat(persenan) / 100)).toFixed(
      3
    ),
    totalBayar: (
      parseFloat(bayarTrf) +
      parseFloat(bayarTunai) +
      parseFloat(totalCiok) +
      parseFloat(totalRosok) +
      parseFloat(totalGiro)
    ).toFixed(3),
    StatusTitipan: StatusTitipan,
  };
})(FormTitipanSupplier);
