import React, { Component } from "react";
import { createNumberMask } from "redux-form-input-masks";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class AccordCashMulti extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    console.log(this.props.sumTunai);
    this.props.change("cash", this.props.sumTunai);
    this.props.change("cashNetto", parseFloat(this.props.sumNetto).toFixed(3));
  }

  onChange() {
    this.props.change("cashNetto", parseFloat(this.props.sumNetto).toFixed(3));
    this.props.change("cash", this.props.sumTunai);
  }
  render() {
    console.log();
    return (
      <div className="col-lg-6 mt-3">
        <div id="accordion" className="custom-accordion mb-4">
          <div className="card mb-0 bg-dark">
            <div className="card-header " id="headingOne">
              <div className="row">
                <div className="col-lg-8">
                  <h5 className="m-0">
                    <a
                      className="custom-accordion-title d-block pt-2 pb-2 text-white"
                      data-toggle="collapse"
                      href="#collapseCash"
                      aria-expanded="true"
                      aria-controls="collapseCash"
                    >
                      Cash
                    </a>
                  </h5>
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="" style={{ color: "white" }}>
                    Netto
                  </label>
                  <Field
                    name="cashNetto"
                    type="number"
                    component="input"
                    className="form-control"
                    readOnly={true}
                  />
                </div>
              </div>
            </div>
            <div
              id="collapseCash"
              className="collapse"
              aria-labelledby="headingOne"
              data-parent="#accordion"
            >
              <div className="card-body bg-white">
                <form onSubmit={this.props.handleSubmit}>
                  <div className="row">
                    <div className="col-lg-2">
                      <label htmlFor="">.</label>
                      <div className="form-group">
                        <button
                          className="btn btn-primary btn-block"
                          type="button"
                          data-target="#cashModal"
                          data-toggle="modal"
                        >
                          +
                        </button>
                      </div>
                    </div>
                    <div className="col-md-5 col-lg-5">
                      <label htmlFor="">Cash</label>
                      <Field
                        name="cash"
                        component="input"
                        type="tel"
                        readOnly={true}
                        className="form-control"
                        onBlur={this.onChange()}
                        {...currencyMask}
                      />
                    </div>
                    <div className="col-md-5 col-lg-5">
                      <Field
                        name="cashNetto"
                        label="Netto"
                        type="number"
                        component={renderField}
                        readOnly={true}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button type="submit" className="btn btn-primary">
                          Simpan
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>{" "}
          {/* end card*/}
        </div>
      </div>
    );
  }
}

AccordCashMulti = reduxForm({
  form: "AccordCashMulti",
  enableReinitialize: true,
})(AccordCashMulti);
export default connect((state) => {
  return {};
})(AccordCashMulti);
